<template>
  <div
    class="modal fade"
    id="exampleModal"
    tabindex="-1"
    aria-labelledby="exampleModalLabel"
    aria-hidden="true"
    ref="modal"
  >
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header bg-danger">
          <h5 class="modal-title text-white" id="exampleModalLabel">
            <span v-if="item.type === 'order'"></span>
            <span v-else-if="item.type === 'coupon'"></span>
            <span v-else-if="item.type === 'product'"></span>
          </h5>
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="modal"
            aria-label="Close"
          ></button>
        </div>
        <div class="modal-body">
           <span v-if="item.type === 'order'">{{item.id}}</span>
            <span v-else-if="item.type === 'coupon'">-{{item.title}}</span>
            <span v-else-if="item.type === 'product'">-{{item.title}}</span> ?
        </div>
        <div class="modal-footer">
          <button
            type="button"
            class="btn btn-secondary"
            data-bs-dismiss="modal"
          >
            
          </button>
          <button
            type="button"
            class="btn btn-outline-danger"
            @click="$emit('del-item', item.id)"
          >
            
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import modalMixin from '@/mixins/modalMixins'
export default {
  data () {
    return {
      modal: ''
    }
  },
  props: {
    item: Object
  },
  mixins: [modalMixin]
}
</script>
